<template>
  <div class="slider-main">
    <span class="span-class">{{ msg + '%' }}</span>
    <div class="slider-content">
      <div
        class="slider-show"
        :style="{width: msg + '%'}"
      ></div>
    </div>
  </div>
</template>
<style scoped>
.slider-main {
  line-height: 20px;
  border-radius: 10px;
  width: 100%
}
.slider-content {
  background-color: #fee3e6;
  width: 100%;
  height: 5px;
  border-radius: 5px;
}
.slider-show {
  background-color: #f75464;
  height: 5px;
  border-radius: 5px;
}
.span-class {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 30px;
  color: #f75464;
  border-radius: 5px;
}
</style>
<script>
export default {
  name: "SliderBar",
  model: {
      prop: 'msg',
      event: 'update'
  },
  props: {
      msg: Number
  },
  data() {
    return {
      value_self: this.msg
    };
  }
};
</script>





